.App {
  display: flex;
  flex-direction: column;
}

.App-header {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  color: white;
  background-color: rgb(229 239 235);
  padding: 10px;
  box-shadow: 0 2px 4px -2px rgba(0,0,0,.2);
}

.App-main {
  display: flex;
  flex-direction: row;
}

.App-graph {
  flex-grow: 10;
}

.download-logo-container {
  display: flex;
}

.App-sidebar {
  box-shadow: -2px 0 2px -2px rgba(0,0,0,.2);
  flex-grow: 3;
  flex-basis: 10em;
}

.note-main {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 2em;
}

.notas-logo {
  height: 1.25em;
  width: auto;
  /*box-shadow: 0px 0px 10px #fff;*/
}

